
.hero {
  position: relative;
  text-align: center;
  color: var(--gray-0);
  min-height: 52vh;
  background-size: cover;
  background-position: center;
}
.hero::after {
  content: "";
  position: absolute; inset: 0;
  background: linear-gradient(to bottom, rgba(0,0,0,.55), rgba(0,0,0,.35), rgba(0,0,0,.2));
}
.hero-overlay {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 52vh;
  padding: var(--sp-12) var(--sp-4) var(--sp-10);
}
.hero h1 {
  font-family: var(--font-accent);
  font-size: var(--fs-900);
  line-height: 1.1;
  margin: 0;
}
.hero-subtitle {
  font-size: var(--fs-600);
  margin-top: var(--sp-2);
  max-width: 70ch;
}
